body {
  width: 180px;
  height: 370px;
  padding-top: 10px;
  padding-left: 5px;
  padding-right: 5px;
  overflow-x: hidden;
}

tr {
  width: 100%;
}

.screen-name {
  fill: #fff;
  stroke-width: 2px;
  stroke: #77a6f7;
  overflow: auto;
}

.screen-name:hover {
  fill: #303c6c;
  stroke-width: 2px;
  stroke: #77a6f7;
}

.number-label {
  width: 2em;
  height: 43px;
  border-right: 1px solid #ddd;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}

